import * as React from "react";
import { List, Datagrid, TextField, EmailField,
    Show,
    Create,
    Edit,
    SimpleShowLayout,
    SimpleForm,
    TextInput,
    SelectInput,
    Filter
    } from 'react-admin';
    
    const PostFilter = (props) => (
        <Filter {...props}>
            <TextInput label="Display Name" source="displayName" alwaysOn />
        </Filter>
    );

export const requstsList = props => (
    <List filters={<PostFilter />} {...props}>
        <Datagrid rowClick="edit">
            <TextField source="id" />
            <TextField source="displayName" />
            <EmailField source="email" />
            <TextField source="university" />
            <TextField source="role" />
            <TextField source="status" />
        </Datagrid>
    </List>
);
export const requstsShow = (props) => (
    <Show {...props}>
      <SimpleShowLayout>
        <TextField source="id" />
        <TextField source="displayName" />
        <TextField source="username" />
        <TextField source="createdate" />
        <TextField source="lastupdate" />
        <TextField source="role" />
        <TextField source="status   " />
      </SimpleShowLayout>
    </Show>
);
export const requstsEdit = (props) => (
    <Edit {...props}>
      <SimpleForm>
        <TextField source="id" />
        <TextInput source="displayName" />
        <TextInput source="email" />
        <TextInput source="university" />
        <TextInput source="role" />
        <SelectInput source="status" choices={[
        { id: "Accepted", name: 'Accepted' },
        { id: "Rejected", name: 'Rejected' }
      ]} />         
      </SimpleForm>   
         </Edit>
);
export const requstsCreate = (props) => (
    <Create {...props}>
      <SimpleForm>
        <TextInput source="id" />
        <TextInput source="displayName" />
        <TextInput source="email" />
        <TextInput source="university" />
        <TextInput source="role" />
        </SimpleForm>   
         </Create>
);
